import axios from 'axios'

const instance = axios.create({
  baseURL: import.meta.env.VITE_API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
})

// 🔐 Har bir so‘rovga tokenni avtomatik qo‘shish
instance.interceptors.request.use((config) => {
  const token = localStorage.getItem('access_token')
  if (token) {
    config.headers.Authorization = `Bearer ${token}`
  }

  return config
})

// 🔁 401 bo‘lsa avtomatik refresh qilish
instance.interceptors.response.use(
  (response) => response,
  async (error) => {
    const originalRequest = error.config

    if (error.response?.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true
      try {
        const refresh = localStorage.getItem('refresh_token')

        if (!refresh) throw new Error('Refresh token mavjud emas')

        const refreshRes = await axios.post(
          `${import.meta.env.VITE_API_BASE_URL}/token/refresh/`, // ✅ yangilangan endpoint
          { refresh },
          { headers: { 'Content-Type': 'application/json' } },
        )

        const newAccess = refreshRes.data.access
        localStorage.setItem('access_token', newAccess)

        // 🔄 Yangi token bilan so‘rovni qayta yuborish
        originalRequest.headers.Authorization = `Bearer ${newAccess}`
        return instance(originalRequest)
      } catch (refreshError) {
        // 🔒 Agar refresh token ham eskirgan bo‘lsa — logout
        localStorage.removeItem('access_token')
        localStorage.removeItem('refresh_token')
        window.location.href = '/login'
        return Promise.reject(refreshError)
      }
    }

    return Promise.reject(error)
  },
)

export default instance
