import { defineStore } from 'pinia'
import axios from '@/services/axios'
import api from '@/services/api'

interface User {
  id: number
  phone: string
  full_name?: string
  role: 'admin' | 'commission' | 'applicant'
  profile?: any
  diplom?: any
  transfer_diplom?: any
  application?: any
}

interface AuthState {
  accessToken: string | null
  refreshToken: string | null
  user: User | null
  isAuthenticated: boolean
  phone: string | null
  isVerified: boolean
}

export const useAuthStore = defineStore('auth', {
  state: (): AuthState => ({
    accessToken: localStorage.getItem('access_token'),
    refreshToken: localStorage.getItem('refresh_token'),
    user: null,
    isAuthenticated: !!localStorage.getItem('access_token'),
    phone: null,
    isVerified: false,
  }),

  actions: {
    async startAuth(phone: string) {
      await axios.post(api.auth(), { phone })
      this.phone = phone
      this.isVerified = false
    },

    async verifyCode(code: string) {
      const res = await axios.post(api.verify(), {
        phone: this.phone,
        code,
      })

      console.log('🧪 /auth/verify/ javobi:', res.data)

      const { access, refresh } = res.data

      this.accessToken = access
      this.refreshToken = refresh
      this.isAuthenticated = true
      this.isVerified = true

      localStorage.setItem('access_token', access)
      localStorage.setItem('refresh_token', refresh)

      // ✅ user ma’lumotlarini verify javobida yo‘q — endi alohida /auth/me/ dan olib kelamiz
      await this.fetchUser()
    },

    async fetchUser() {
      try {
        const res = await axios.get(api.me()) // ✅ To‘g‘ri URL
        const role = typeof res.data.role === 'string' ? res.data.role.toLowerCase() : null
        this.user = {
          ...res.data,
          role,
        }
        this.isAuthenticated = true
        console.log('✅ /auth/me foydalanuvchi:', res.data)
      } catch (error) {
        console.error('❌ fetchUser error:', error)
        this.logout()
      }
    },

    async refreshAccessToken() {
      try {
        const res = await axios.post(api.refreshToken(), {
          refresh: this.refreshToken,
        })
        this.accessToken = res.data.access
        localStorage.setItem('access_token', res.data.access)
      } catch {
        this.logout()
      }
    },

    async logout() {
      try {
        if (this.refreshToken) {
          await axios.post(api.logout(), { refresh: this.refreshToken })
        }
      } catch {
        console.error('❌ logout error')
      }
      this.accessToken = null
      this.refreshToken = null
      this.user = null
      this.phone = null
      this.isVerified = false
      this.isAuthenticated = false
      localStorage.removeItem('access_token')
      localStorage.removeItem('refresh_token')
    },
  },

  getters: {
    hasSubmitted: (state) => {
      const user = state.user
      if (!user) return false // foydalanuvchi hali yuklanmagan

      const hasPassport = !!user.profile
      const hasDiplom = !!user.diplom || !!user.transfer_diplom
      const hasApplication = !!user.application

      return hasPassport && hasDiplom && hasApplication
    },

    role: (state) => state.user?.role || null,
    isAdmin: (state) => state.user?.role === 'admin',
    isCommission: (state) => state.user?.role === 'commission',
    isApplicant: (state) => state.user?.role === 'applicant',
  },
})
